import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export const store = new Vuex.Store({
	state:{
		isUserRegistered: false,
		baseRequestUrl: "http://5.45.120.170/evo-donor/",
		place:"",
		link: "",
		quizComponent: "q-start",
		quizCounter: 1,
		quiz:[
		{technic: "technick"},
		{question:1,
		 image:'http://photodnepr.com.ua/dist/calendar.svg',
		title:"  18  60 ?",
		answer:{
			image:'url("./src/components/assets/error.svg"),url("./src/components/assets/calendar.svg")',
		title:" ,        18-    60-.",
		}},

		{question:2,
		 image:'http://photodnepr.com.ua/dist/ageweight.svg',
		title:"   50     150 ?",
		answer:{
			image:'url("/src/components/assets/error.svg"),url("/src/components/assets/calendar.svg")',
		title:"       .   ,       .      .",
		}},

			{question:3,
		 image:'http://photodnepr.com.ua/dist/plane.svg',
		title:"      ?",
		answer:{
			image:'url("/src/components/assets/error.svg"),url("/src/components/assets/calendar.svg")',
		title:" ,     6   3- ,    .",
		}},

			{question:4,
		 image:'http://photodnepr.com.ua/dist/heartcardio.svg',
		title:"    ,   ?",
		answer:{
			image:'/src/components/assets/error.svg"),url("/src/components/assets/calendar.svg")',
		title:" ,      .",
		}},

			{question:5,
		image:'http://photodnepr.com.ua/dist/baby.svg',
		title:" ,       ?",
		answer:{
			image:'url("/src/components/assets/error.svg"),url("/src/components/assets/calendar.svg")',
		title:"          .    ,     3 ,   .",
		}},

			{question:6,
		 image:'http://photodnepr.com.ua/dist/allicon.svg',
		title:"  ,    ?",
		answer:{
			image:'url("/src/components/assets/error.svg"),url("/src/components/assets/calendar.svg")',
		title:"       12        .",
		}},

			{question:7,
		image:'http://photodnepr.com.ua/dist/endoscopy.svg',
		title:"        ?",
		answer:{
			image:'url("/src/components/assets/error.svg"),url("/src/components/assets/calendar.svg")',
		title:"   6   ,      .",
		}},

			{question:8,
		 image:'http://photodnepr.com.ua/dist/transfusion.svg',
		title:"     ?",
		answer:{
			image:'url("/src/components/assets/error.svg"),url("/src/components/assets/calendar.svg")',
		title:"     1      (     ,   -)",
		}},
		],
		userId:""
	},

	mutations: {
		answer(state){
			state.quizComponent = "q-answer"
		},
		refresh(state){
			state.quizCounter = 1;
			state.quizComponent = "q-start"
		},
		final(state){
			state.quizComponent = "q-final"
		},
		cNext(state){
			state.quizCounter++;
		},
		formLink(state){
			state.link = "https://www.google.com/maps/embed/v1/search?q=++++" + state.place + "&key=AIzaSyAO0Ju6FOiKKWYpbv-CDHJ9aq9Ff4NOy9g"
		}
	}
	
})



